"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import Marquee from "react-fast-marquee";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import BreakingNews from "../../components/newssection/BreakingNews";
import { getCategories } from "@/services/newsCategory.service";

export default function NewsHome() {
  const [news, setNews] = useState<any[]>([]);
  const [categories, setCategories] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const newsRes = await getNews();
        const categoriesRes = await getNewsCategories(); // جلب التصنيفات

        const newsArray = Array.isArray(newsRes.data) ? newsRes.data : newsRes.data.data;
        const categoriesArray = Array.isArray(categoriesRes.data)
          ? categoriesRes.data
          : categoriesRes.data.data;

        setNews(newsArray || []);
        setCategories(categoriesArray || []);
      } catch (err) {
        console.error("Fetch news error", err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // تصفية الأخبار حسب التصنيف
  const newsByCategory = (categorySlug: string) =>
    news.filter((n) => n.category?.slug === categorySlug);

  return (
    <div className="bg-white">
      <Header />

      {/* قسم الهيرو */}
      <section
        className="relative bg-cover bg-center text-white"
        style={{ backgroundImage: "/كرتاخضر.jpg" }}
      >
        <div className="bg-black/70 px-8 py-24 text-center">
          <h2 className="text-4xl md:text-5xl font-bold mt-4">
            كـل اخبــار <span className="text-red-500">الدرامـــا</span>
          </h2>
          <p className="mt-4 text-lg max-w-2xl mx-auto">
            موقع يمن ميديا - مصدركم الموثوق لآخر الأخبار والتقارير الحصرية عن
            الدراما اليمنية و الممثلين
          </p>
          <div className="mt-6 flex justify-center space-x-4 space-x-reverse">
            <button className="bg-red-600 px-6 py-2 rounded-md font-semibold hover:bg-red-700 transition">
              تابع أخبار الدراما و نجوم الشاشة اليمنيه
            </button>
          </div>
        </div>
      </section>

      {/* شريط الأخبار العاجلة */}
      <div className="bg-red-600 text-white py-2 text-sm">
        <div className="container mx-auto flex items-center gap-2">
          <Marquee pauseOnHover gradient={false} speed={50}>
            {news
              .filter((n) => n.is_breaking)
              .map((n) => (
                <span key={n.uuid} className="mx-4">
                  {n.title}
                </span>
              ))}
          </Marquee>
        </div>
      </div>

      <BreakingNews />

      {/* قسم آخر الأخبار حسب التصنيفات */}
      <section className="py-10 px-6 bg-black">
        <h3 className="text-2xl font-bold mb-6 text-white">الأخبار</h3>

        {loading ? (
          <p className="text-gray-400 text-center py-6 animate-pulse">
            جارٍ تحميل الأخبار...
          </p>
        ) : categories.length === 0 ? (
          <p className="text-gray-400 text-center py-6">لا يوجد تصنيفات</p>
        ) : (
          categories.map((cat) => {
            const catNews = newsByCategory(cat.slug);
            if (catNews.length === 0) return null;

            return (
              <div key={cat.id} className="mb-10">
                <h4 className="text-xl font-semibold text-red-500 mb-4">{cat.name}</h4>
                <div className="grid md:grid-cols-3 gap-6">
                  {catNews.map((item) => (
                    <div
                      key={item.uuid}
                      className="bg-white rounded-lg shadow-md overflow-hidden border"
                    >
                      <img
                        src={item.image_url || `https://picsum.photos/600/400?random=${item.id}`}
                        alt={item.title}
                        className="w-full h-48 object-cover"
                      />
                      <div className="p-4">
                        <h5 className="font-bold text-lg mb-2">{item.title}</h5>
                        <p className="text-gray-600 text-sm mb-3">
                          {item.summary || "هذا النص هو ملخص قصير للخبر."}
                        </p>
                        <Link
                          href={`/news/${item.uuid}`}
                          className="text-red-600 hover:underline text-sm font-semibold"
                        >
                          اقرأ المزيد ←
                        </Link>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            );
          })
        )}
        <div className="mt-6 text-right">
          <Link href="/news" className="text-red-600 font-semibold">
            ← جميع الأخبار
          </Link>
        </div>
      </section>
      <Footer />
    </div>
  );
}
